.card{
    margin: 2rem 0;
    &:hover{
        cursor: pointer;
        transition: all 0.3s cubic-bezier(.25,.8,.25,1);
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    }
    h3{
        font-family: "Lato", sans-serif;
    }
    h5{
        font-family: 'Lato', sans-serif;
        font-size: 13px;
        font-weight: 600;
        color: #2f435e;
    }
    
    .cart-icon{
        color: #FFF;
    }
    .heart-icon{
        border-color: red;
        color: red;
        margin-left: 17px;
        
    }
}

